import { SetStateAction } from "react"
import { AutosuggestSource } from "@rp/react/tanstack-api/content"
import { SourceSelectorSelectedSourceInfoV2 } from "../sourceSelector.types"

type ToggleSourceSelectionFuncProps = {
  e: React.MouseEvent<HTMLDivElement>
  allAutosuggestSources: Array<AutosuggestSource>
  selectedSources: Array<SourceSelectorSelectedSourceInfoV2>
  setSelectedSources: (value: SetStateAction<Array<SourceSelectorSelectedSourceInfoV2>>) => void
  hasReachedMaxSelection: boolean
  showMaxSelectionErrorToast: () => void
  planComparisonHandleOpen: () => void
}

function toggleSourceSelectionFuncV2({
  e,
  selectedSources,
  setSelectedSources,
  allAutosuggestSources,
  hasReachedMaxSelection,
  showMaxSelectionErrorToast,
  planComparisonHandleOpen,
}: ToggleSourceSelectionFuncProps): void {
  const id = e.currentTarget.getAttribute("data-id")
  const entitled = e.currentTarget.getAttribute("data-entitled") === "true"

  if (!entitled) {
    planComparisonHandleOpen()
    return
  }

  if (!id) return

  if (hasReachedMaxSelection) {
    showMaxSelectionErrorToast()
    return
  }

  const idx = selectedSources.findIndex(source => source.id === id)

  // removing source

  if (idx !== -1) {
    setSelectedSources(selectedSources => [
      ...selectedSources.slice(0, idx),
      ...selectedSources.slice(idx + 1),
    ])
    return
  }

  // adding source

  const newSource = allAutosuggestSources.find(autosuggestSource => autosuggestSource.id === id)

  if (!newSource) return

  const newSourceSelection: SourceSelectorSelectedSourceInfoV2 = {
    id,
    name: newSource.name,
    categoryID: newSource.category,
  }

  setSelectedSources(selectedSources =>
    [newSourceSelection, ...selectedSources].sort((a, b) => a.name.localeCompare(b.name)),
  )
}

export { toggleSourceSelectionFuncV2 }
